@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fffcf9;
}

html,
body {
  overflow: auto;
  height: 100vh;
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.435);
  border-radius: 20px;
}

body {
  position: relative;
  background: linear-gradient(45deg, #102d71 0%, #000000 52%, #c12a23 100%) no-repeat;
}

main {
  position: absolute;
  height: 100%;
  right: 0;
  left: 0;
}
main header {
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 126px auto 30px auto;
}
main header .logo {
  margin: 0 auto;
  width: 87.24px;
  height: 62.93px;
}
main header h1 {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 40px;
}
main section {
  display: flex;
  max-width: 592px;
  margin: 0 auto;
  align-items: center;
  gap: 34px;
}
main section img {
  width: 171px;
  height: 243.77px;
}
main section h2 {
  font-weight: 700;
  line-height: 30px;
  margin-bottom: 19px;
}
main section p {
  max-width: 423px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 20px;
}
main section p img {
  width: 20px;
  height: 15px;
}
main section .description {
  height: 250px;
  overflow: auto;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
main section .scrooll-style::-webkit-scrollbar {
  width: 12px;
  border-radius: 10px;
  scrollbar-width: 5px;
  background-color: #aaa;
}
main section .scrooll-style::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.435);
  border-radius: 20px;
}
main section p.scrooll-style {
  background-color: #00000084;
  opacity: 0.8;
  border-radius: 10px;
}
main .div-button {
  position: relative;
  height: 150px;
}
main .div-button button {
  position: absolute;
  left: 0;
  right: 0;
  width: 180.98px;
  height: 47px;
  margin: 30px auto auto;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 17px;
  border-radius: 5px;
  border: none;
  font-size: 12px;
  font-weight: 700;
}
main .div-button button img {
  width: 35.56px;
  height: 25px;
}
main .div-button p {
  position: absolute;
  max-width: 381px;
  margin: 0 auto 10px;
  right: 0;
  left: 0;
  bottom: 1px;
  text-align: center;
}

@media (max-width: 425px) {
  main header {
    text-align: center;
    width: 359px;
    height: 193px;
    margin-top: 50px;
  }
  main .telaErro {
    height: 329px;
  }
  main section {
    display: grid;
    justify-items: center;
    width: 316px;
    height: 662.62px;
  }
  main section h2 {
    text-align: center;
  }
  main section p {
    font-size: 17px;
  }
}

